<template>
    <div class="chart">
        <h1 class="title">近一周收入支出图表</h1>
        <div class="loading" v-if="tableLoading">
            <md-activity-indicator :size="35" vertical class="md-activity-indicator-css">加载中...
            </md-activity-indicator>
        </div>
        <div>
            <md-chart :size="['7rem', '4rem']"
                    :lines="30"
                    :labels="weeks"
                    :datasets="chartData"
            ></md-chart>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'mand-mobile'
    export default {
        name: "chart",
        data() {
            return {
                dict: ['周日', '周一', '周二', '周三', '周四', '周五', '周六'],
                weeks: [],
                chartData: [
                    {
                        color: '#5e64ff',
                        width: 1,
                        values: []
                    },
                    {
                        width: 1,
                        values: []
                    }
                ],
                tableLoading: false
            }
        },
        methods: {
            fetchTotalData() {
                const url = '/api/getTotalItems';
                this.tableLoading = true;
                this.$http.get(url).then(res => {
                    this.formatData(res.data.data);
                    this.tableLoading = false;
                }).catch(() => {
                    this.tableLoading = true;
                    Toast.failed('数据获取失败')
                })
            },
            formatData(data) {
                for (let i = 0; i < 7; i++) {
                    this.chartData[0].values.push(data[i].inCount);
                    this.chartData[1].values.push(data[i].outCount);
                }
            }
        },
        created() {
            const day = new Date().getDay();
            for (let j = day; j < this.dict.length; j++) {
                this.weeks.push(this.dict[j]);
            }
            for (let i = 0; i < day; i++) {
                this.weeks.push(this.dict[i]);
            }
            this.fetchTotalData();
        }
    }
</script>

<style lang="less" scoped>
    .chart {
        padding-top: 0.4rem;
        padding-right: 0.4rem;

        .title {
            font-size: 0.35rem;
            text-align: center;
        }
    }
    .loading .md-activity-indicator-css {
        margin-top: 40%;
        font-size: 0.4rem;
    }
</style>
